<template>
    <div class="nes_msg_detail">
        <navigation-bar
                @onLeftClick="goBack"
                pageName="消息详情"
        ></navigation-bar>
        <div class="detail" v-if="detail">
            <div class="head">
                <img v-if="detail.headPic" :src="detail.headPic" alt="">
                <img v-else src="../../assets/imgs/find/avatar.png" alt=""/>
                <div>{{detail.nickName}}</div>
            </div>
            <div class="content">{{detail.content}}</div>
            <div class="img-box">
                <van-row
                        class="message-img"
                        v-bind:class="{'big-image':detail.notePic.length == 1}"
                        v-if="detail.notePic">
                    <van-col span="6" v-for="(imgurl,index) in detail.notePic" v-bind:key="index">
                        <img @click="preview(detail.notePic,index)" alt v-lazy="imgurl" :key="imgurl"/>
                    </van-col>
                </van-row>
            </div>
            <div class="operate-box">
                <div class="left">
                    <div class="time">{{detail.createdTime}}</div>
                    <!--<div class="del">删除</div>-->
                </div>
                <div class="right">
                <span class="" @click="setLike(detail.noteCode,detail.likerList)">
                  <img class="cls-icon" src="../../assets/imgs/find/unlove_icon.png" alt="" v-if="checkLike(detail.likerList)">
                  <img class="cls-icon" src="../../assets/imgs/find/love_icon.png" alt="" v-else>
                  <span v-if="detail.likerList">{{detail.likerList.length}}</span>
                </span>
                  <span class="replay" @click="addComments(detail.noteCode)">
                  <!-- <van-icon name="chat-o" size="15px" /> -->
                  <img class="cls-icon" src="../../assets/imgs/find/com_icon.png" alt="">
                  {{detail.commentList?detail.commentList.length:0}}
                </span>
                </div>
            </div>
            <div class="reply-container" v-if="detail.likerList.length > 0 || detail.commentList.length > 0" >
                <van-row type="flex" class="goods-group" v-if="detail.likerList.length > 0">
                <span class="class-span">
                  <img class="cls-icon" src="../../assets/imgs/find/praise_icon.png" alt="">
                </span>
                    <van-col class="goods-person">
                  <span v-for="(ele,a) in detail.likerList" v-bind:key="a">
                    {{ele.nickName}}
                    <i v-if="a != detail.likerList.length-1">,</i>
                  </span>
                    </van-col>
                </van-row>
                <div v-if="detail.commentList">
                    <div type="flex" class="reply-group" v-for="(eva,d) in detail.commentList" v-bind:key="d">
                        <div class="reply-item">
                            <div class="reply-desc" @click="replyAction(eva,detail.noteCode)">
                      <span
                              class="reply-name"
                              v-bind:class="{'reply-flag':!eva.returner}"
                      >
                        {{eva.nickName}}
                        <img
                                v-if="eva.isProperty == 1"
                                src="../../assets/imgs/find/property.png"
                                width="36px;"
                        />
                      </span>
                                <span class="result" v-if="eva.returner">回复</span>
                                <span
                                        class="reply-name"
                                        v-bind:class="{'reply-flag':eva.returner}"
                                >{{eva.returner}}</span>
                                <p class="reply-text">{{eva.content}}</p>
                            </div>
                            <van-row>
                                <van-col span="6" v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                                    <img @click="preview(eva.evalPic,c)" :src="ind" class="small-reply-img" alt/>
                                </van-col>
                            </van-row>
                        </div>
                    </div>
                </div>
                <van-row></van-row>
            </div>
        </div>
        <van-action-sheet
                v-model="deleteShow"
                :actions="deleteActions"
                cancel-text="取消"
                round
                close-on-click-action
                @select="delCommentAction"
        />
        <Preview :images="images" ref="myPreview" @indexChange="indexChange"></Preview>
    </div>
</template>

<script>
    import {getNewMsgDetailService, saveLikeService, delCommentInforService} from "../../services/newFindService";
    import {store} from "../../utils/store";
    import { ImagePreview } from "vant";
    import Preview from "@/components/preview/Preview.vue";
    import typeJudge from "../../utils/typeJudge";
    import NavigationBar from "../title/NavigationBar";
    export default {
        name: "NewMsgDetail",
        mounted() {
            /*this.userInfo = store.get("userInfo");
            this.noteCode = this.$route.params.noteCode;
            this.getDetail();*/
        },
        activated() {
            if (this.$route.params.refreshDetail) {
                this.resetDetailData();
                this.userInfo = store.get("userInfo");
                this.noteCode = this.$route.params.noteCode;
                this.getDetail();
            }
        },
        data() {
            return {
                deleteActions: [{name: "删除"}],
                deleteShow: false,
                detail: null,
                noteCode: "",
                isLikeSetting: false,
                images: [],
                imgComps: [],
            };
        },
        components: {
            Preview: Preview,
            NavigationBar
        },
        methods: {
            resetDetailData() {
                this.deleteShow = false;
                this.detail = null;
                this.noteCode = "";
                this.isLikeSetting = false;
            },
            goBack() {
                this.$router.back(-1);
            },
            getDetail() {
                getNewMsgDetailService({
                    noteCode: this.noteCode
                }).then(
                    result => {
                        if (result.resultCode == "0") {
                            this.detail = result.data;
                        } else {
                            this.$toast(result.msg);
                        }
                    },
                    error => {
                        this.$toast(error);
                    }
                );
            },
            checkLike(likeMap) {
                if (!likeMap) {
                    return false;
                }
                return likeMap.some(val => val.userCode == this.userInfo.USER_CODE);
            },
            setLike(noteCode, likeMap) {
                if (this.isLikeSetting) {
                    console.log("like is setting.");
                    return;
                }
                this.isLikeSetting = true;
                let reqData = {
                    "concemflag": 1,
                    "isProperty": 1,
                    "noteCode": noteCode,
                    "userCode": this.userInfo.USER_CODE
                };

                if (likeMap) {
                    reqData.concemflag = likeMap.some(val => val.userCode == reqData.userCode)
                        ? 0
                        : 1;
                } else {
                    reqData.concemflag = 1;
                }
                saveLikeService(reqData)
                    .then(res => {
                        this.isLikeSetting = false;
                        this.getDetail();
                    }, err => {
                        this.isLikeSetting = false;
                    })
                    .catch(error => {
                        this.isLikeSetting = false;
                    })
            },
            replyAction(evaMap, noteMap) {
                if (this.userInfo.USER_CODE === evaMap.userCode) {
                    this.deleteShow = true;
                    this.delActionObj = {
                        noteCode: noteMap,
                        evalCode: evaMap.evalCode
                    };
                } else {
                    let replys = Object.assign(typeJudge.objectFullCopy(evaMap), {
                        NOTE_CODE: noteMap
                    });
                    this.asyncUpdateDetail();
                    this.$router.push({
                        name: "addNote",
                        params: {pageType: "reply", replyParam: JSON.stringify(replys), fromNewMsgDetail: 1}
                    });
                }
            },
            preview(src, index) {
                let urls = src.map(val => val);
                // ImagePreview({ images: urls, startPosition: index });
                this.images = urls;
                this.indexChange(index);
                this.$refs.myPreview.setIndex(index);
                this.$refs.myPreview.setIsShowPreview(true);
            },

            /**
             * 图片切换
             **/
            indexChange(index) {
                var src = this.images[index];
                if(src.lastIndexOf("@") == -1) {
                    return;
                }
                var index1 = src.lastIndexOf(".");
                var index2 = src.lastIndexOf("@");
                var newSrc = src.substring(0, index2) + src.substring(index1, src.length);
                var img;
                if (this.imgComps.length == 0 || !this.imgComps[index]) {
                  this.imgComps[index] = new Image();
                } else {
                  this.imgComps[index].onload = null;
                }
                img = this.imgComps[index];
                // var img = new Image();
                img.onload = () => {
                    this.$set(this.images, index, newSrc);
                }
                img.src = newSrc;
            },
            addComments(noteCode) {
                this.asyncUpdateDetail();
                this.$router.push({
                    name: "addNote",
                    params: {pageType: "comments", noteCode, fromNewMsgDetail: 1 }
                });
            },
            asyncUpdateDetail() {
                store.set("updateNewMsgDetail", () => {
                    this.getDetail();
                });
            },
            delCommentAction() {
                let { noteCode, evalCode } = this.delActionObj;
                delCommentInforService(noteCode, evalCode)
                    .then(() => {
                        this.getDetail();
                    })
            },
        },
    }
</script>

